import React from 'react';
import { connect } from 'react-redux';
import { startUpdateOrderStatus } from '../actions/order';
import {StatusTypes, ToppingTypes, PizzaTypes} from '../enums/order';

class OrdersListItem extends React.Component{
    constructor(props){
        super(props);

        this.state = {
            Id: props.Id, 
            CustomerId: props.CustomerId, 
            ToppingType: props.ToppingType, 
            PizzaType: props.PizzaType, 
            Status: props.Status
        };
    }

    createStatusOptionsList = () => {
        let optionsList = [];

        for(let index = 1; index < StatusTypes.length; index += 1){
            let jsx = (
                <option selected={index === this.state.Status}>
                    {StatusTypes[index]}
                </option>
            );
            optionsList.push(jsx);
        }
    };

    handleOrderStstusChange = (e) => {
        this.setState({
            Status: e.target.value
        });
        startUpdateOrderStatus(this.state);
        this.props.history.push('/');
    };

    render(){
        return (
            <section>
                <p>Type: {PizzaTypes[this.state.PizzaType]}</p>
                <p>Topping: {ToppingTypes[this.state.ToppingType]}</p>
                <p>Current status: 
                    <select 
                        onChange={this.handleOrderStstusChange}
                    >
                        {this.createStatusOptionsList}
                    </select>
                </p>
            </section>
        );
    };
}

const mapDispatchToProps = (dispatch) => ({
    startUpdateOrderStatus: ({Id,CustomerId,ToppingType,PizzaType,Status}) => dispatch(startUpdateOrderStatus({Id,CustomerId,ToppingType,PizzaType,Status}))
});
export default connect(undefined,mapDispatchToProps)(OrdersListItem);


